@import url(https://fonts.googleapis.com/css2?family=Thasadith:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Francois+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper {
  height: auto;
  margin: 0 auto;
}
.header{background-color:#1cab9c;height:70px;width:100%;display:flex;justify-content:space-between;margin-bottom:25px;z-index:9999999}.header .logo-container{display:flex;justify-content:flex-start;align-items:center;height:100%;width:25%;padding:15px}.header .logo-container .logo{width:20%}.header .logo-container .logo-text{font-family:'Francois One', sans-serif;text-shadow:2px 2px grey;color:white;font-size:30px;padding:10px 15px;width:200px}.header .nav-options{width:100%;height:100%;display:flex;align-items:center;justify-content:flex-end;padding-right:40px}.header .nav-options .user{padding-left:20px;color:white}.header .nav-options .usericon{color:white;align-items:center}.header .nav-options .option{color:white;text-shadow:1px 1px grey;padding:10px 15px;cursor:pointer}.header .nav-options .option:hover{background-color:#008E7E}


.sign-up-container{position:relative;width:600px;border:1px solid black;border-radius:5px;padding:20px;margin:0 auto}.buttons .btn{margin:5px}.error{color:red}

.sign-up-container .title{margin:20px 0;text-align:center}


.profile-container{position:relative;width:600px;border:1px solid black;border-radius:5px;padding:20px;margin:0 auto}.buttons .btn{margin:5px}.error{color:red}

.text-container{margin:0 auto;width:900px;text-align:left;padding:60px}.text-container span{font-size:20px}.text-container .company-name{font-size:25px;font-family:'Francois One', sans-serif}.icon{padding-top:10px;width:26%}.about-text{text-shadow:0.1px 0.1px black;color:#008E7E;font-size:16px;display:block;height:200px}



.group{position:relative;width:500px;margin:0 auto;border:1px solid black;border-radius:5px}.group form{padding-left:25px;padding-right:25px;padding-bottom:30px}.group .form-input{font-size:18px;width:100%;margin:10px 0}.group .checkbox-container{display:flex;justify-content:space-evenly}.file-form{padding-bottom:40px}

.card-image{margin:0 auto}.card-image img{width:50% !important;margin:0 auto}


.new-offer-form{width:600px;border:1px solid black;border-radius:5px;padding-bottom:60px}.new-offer-form .form-inputs{padding-left:80px;padding-right:80px}.new-offer-form .form-inputs .offer-fields{padding-top:40px;padding-bottom:40px}

.card{width:300px;height:324px}.card .test .card-action{display:flex;justify-content:space-evenly;height:80px}.card .test .card-action .button{font-size:10px;height:30px}

.button-container{padding:60px 0px}

.footer{position:fixed;left:0;bottom:0;height:50px;width:100%;background-color:#f7eaeb;color:#940909;text-align:center;flex:0 0 50px;display:flex;justify-content:space-between;z-index:100}.footer .icons{width:80%;height:100%;display:flex;align-items:center;justify-content:flex-end;padding-right:40px}.footer .icons .footer-icon{display:flex;justify-content:flex-end;width:80%;display:flex}.comp-name{display:flex;justify-content:flex-end;align-items:center;height:100%;width:100%}

*{box-sizing:border-box}body{font-family:'Thasadith', sans-serif;text-align:center}a{text-decoration:none !important;color:black}.space{padding:60px}

